
import wx from '../../assets/WX.jpg'
import qq from '../../assets/QQ.jpg'
import bom_icon from '../../assets/bom_icon.jpg'
import bom_icon2 from '../../assets/bom_icon2.jpg'
import bom_icon3 from '../../assets/bom_icon3.jpg'
import bom_icon4 from '../../assets/bom_icon4.jpg'
import bomTab1 from '../../assets/bomTab.jpg'
import bomTab2 from '../../assets/bomTab2.jpg'
import bomTab3 from '../../assets/bomTab3.jpg'

import Icon from 'vux/src/components/icon/index.vue'

export default {
	components : {
		Icon
	},
	data (){
		return {
			contact : [
				"  1580000000x   188000000x",
				"  41000000x@qq.com",
				"  xx",
				"  xxx"
			],
			WX : wx,
			wx : false,
			code : [
				{
					src : wx,
					text : ''
				},
				{
					src : qq,
					text : 'QQ'
				}
			],
			copyRight : [
				'  2016 ',
				''
			],
			bom_icon : [
				{src : bom_icon, alt : 'QQ'},
				{src : bom_icon2, alt : 'WX'},
				{src : bom_icon3, alt : 'WB'},
				{src : bom_icon4, alt : 'KJ'}
			],
			bomTab : [
				{src : bomTab1, text : '', title : ''},
				{src : bomTab2, text : 'QQ', title : 'QQ'},
				{src : bomTab3, text : '', title : ''}
			]
		}
	},
	methods : {		
		open (ev){
			
			if(ev.target.alt == 'QQ'){
				window.open ('http://connect.qq.com/widget/shareqq/index.html?url=http://192.168.1.219:8022/&title= ','newwindow','height=500,width=600,top=0,left=0,toolbar=no,menubar=no,scrollbars=no, resizable=no,location=no, status=no')
			}else if(ev.target.alt == 'WB'){
				window.open ('http://service.weibo.com/share/share.php?url=http://128.168.1.219:8022/&title= ','newwindow','height=500,width=600,top=0,left=0,toolbar=no,menubar=no,scrollbars=no, resizable=no,location=no, status=no')
			}else if(ev.target.alt == 'KJ'){
				window.open ('https://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url=http://128.168.1.219:8022/&title= ','newwindow','height=500,width=600,top=0,left=0,toolbar=no,menubar=no,scrollbars=no, resizable=no,location=no, status=no')
			}else if(ev.target.alt == 'WX'){
				this.wx = true
			}
		},
		bomIcon (ev){
			if(ev.target.title == '' || ev.target.innerHTML == '' || ev.target.alt == ''){
				console.log(ev.target);
			}else if(ev.target.title == 'QQ' || ev.target.innerHTML == 'QQ' || ev.target.alt == 'QQ'){
				console.log(ev.target);
			}else if(ev.target.title == '' || ev.target.innerHTML == '' || ev.target.alt == ''){
				
				if(document.documentElement.scrollTop){
					document.documentElement.scrollTop = "0";				
				}else{
					document.body.scrollTop = "0";		
				}
			}
		}
	}
}



